@font-face {
  font-family: 'icons';
  src: @font-icon-url-eot;
  src: @font-icon-url-embedded format('embedded-opentype'),
       @font-icon-url-woff format('woff'),
       @font-icon-url-ttf format('truetype'),
       @font-icon-url-svg format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.font-icon() {
  font-family: 'icons';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  text-align: center;
  vertical-align: baseline;
  line-height: 1;
}

[class^="@{icon}-"], [class*=" @{icon}-"] {
  display: inline-block;
  vertical-align: middle;
  &:before {
    .font-icon;
  }
}

